import LogoTriangulo from "./LogoTriangulo";
import { MessageCircle } from "lucide-react";

const ConstructionPage = () => {
  return (
    <div className="w-full min-h-screen bg-black text-white flex flex-col items-center justify-center p-6">
      <div className="flex flex-col items-center text-center max-w-lg">
        <LogoTriangulo width={200} height={240} />
        <h1 className="text-2xl md:text-3xl font-bold mt-8 mb-3 tracking-tight">
          ¡Estamos construyendo nuestra web!
        </h1>
        <p className="text-lg md:text-xl italic text-gray-400">
          Regresa pronto.
        </p>
      </div>

      {/* Contenedor QR */}
      <div className="mt-12 flex justify-center w-full">
        <div className="w-30 h-30 md:w-50 md:h-50 bg-white rounded-2xl shadow-xl flex items-center justify-center">
          <p className="text-black font-semibold text-lg md:text-xl">
            ContactoQR
          </p>
        </div>
      </div>

      {/* Botón de WhatsApp elegante */}
      <a
        href="https://wa.me/5490000000000" // Reemplaza con tu número
        target="_blank"
        rel="noopener noreferrer"
        className="mt-10 flex items-center gap-3 bg-green-500 hover:bg-green-600 text-black font-semibold py-3 px-6 rounded-2xl shadow-lg transition-all duration-300 text-lg md:text-xl"
      >
        <MessageCircle className="w-6 h-6" />
        WhatsApp
      </a>
    </div>
  );
};

export default ConstructionPage;
